import { useState, useEffect, useCallback } from 'react';
import { useWallet } from '@suiet/wallet-kit';
import { SuiClient } from '@mysten/sui.js/client';
import { BlockchainService } from '../services/blockchainService';

export function useBlockchain() {
  const { 
    connected, 
    account, 
    signAndExecuteTransactionBlock,
    wallet 
  } = useWallet();
  
  const [client, setClient] = useState(null);
  const [blockchainService, setBlockchainService] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Initialize Sui client
  useEffect(() => {
    const suiClient = new SuiClient({
      url: 'https://fullnode.mainnet.sui.io' // or 'https://fullnode.testnet.sui.io' for testnet
    });
    setClient(suiClient);
  }, []);

  // Initialize blockchain service when wallet is connected
  useEffect(() => {
    if (connected && client && wallet) {
      const service = new BlockchainService(client, wallet);
      setBlockchainService(service);
    } else {
      setBlockchainService(null);
    }
  }, [connected, client, wallet]);

  // Create a new event
  const createEvent = useCallback(async (eventData) => {
    if (!blockchainService) {
      setError('Wallet not connected');
      return { success: false, error: 'Wallet not connected' };
    }

    setLoading(true);
    setError(null);

    try {
      const result = await blockchainService.createEvent(eventData);
      setLoading(false);
      return result;
    } catch (err) {
      setLoading(false);
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService]);

  // Purchase a ticket
  const purchaseTicket = useCallback(async (eventId, paymentAmount) => {
    if (!blockchainService) {
      setError('Wallet not connected');
      return { success: false, error: 'Wallet not connected' };
    }

    setLoading(true);
    setError(null);

    try {
      const result = await blockchainService.purchaseTicket(eventId, paymentAmount);
      setLoading(false);
      return result;
    } catch (err) {
      setLoading(false);
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService]);

  // Transfer a ticket
  const transferTicket = useCallback(async (ticketId, recipientAddress) => {
    if (!blockchainService) {
      setError('Wallet not connected');
      return { success: false, error: 'Wallet not connected' };
    }

    setLoading(true);
    setError(null);

    try {
      const result = await blockchainService.transferTicket(ticketId, recipientAddress);
      setLoading(false);
      return result;
    } catch (err) {
      setLoading(false);
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService]);

  // Get user's tickets
  const getUserTickets = useCallback(async () => {
    if (!blockchainService || !account) {
      return { success: false, error: 'Wallet not connected' };
    }

    try {
      const result = await blockchainService.getUserTickets(account.address);
      return result;
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService, account]);

  // Get event details
  const getEventDetails = useCallback(async (eventId) => {
    if (!blockchainService) {
      return { success: false, error: 'Wallet not connected' };
    }

    try {
      const result = await blockchainService.getEventDetails(eventId);
      return result;
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService]);

  // Get all events
  const getAllEvents = useCallback(async () => {
    if (!blockchainService) {
      return { success: false, error: 'Wallet not connected' };
    }

    try {
      const result = await blockchainService.getAllEvents();
      return result;
    } catch (err) {
      setError(err.message);
      return { success: false, error: err.message };
    }
  }, [blockchainService]);

  // Clear error
  const clearError = useCallback(() => {
    setError(null);
  }, []);

  return {
    // State
    connected,
    account,
    loading,
    error,
    
    // Blockchain functions
    createEvent,
    purchaseTicket,
    transferTicket,
    getUserTickets,
    getEventDetails,
    getAllEvents,
    
    // Utility functions
    clearError
  };
} 